import React from 'react'
import { Link } from 'react-router-dom'

const Cv = () => {
    return (
        <div className='main cvpage'>
            <div className='linha'></div>
            <section>
                <h2 className='box'>Résumé</h2>
                <Link
                    to='/docs/CV-Jorge-Soucasaux-Monteiro-2021.pdf'
                    target='_blank'
                    className='download'
                    download>
                    download curriculum vitae<i className='icon-down-open-big'></i>
                </Link>
                <p className='box'>
                    <a href='/projects'>
                        Want to check some projects?{' '}
                        <i className='icon-heart'></i>
                    </a>
                </p>
            </section>
        </div>
    )
}

export default Cv
